import { yupToFormErrors } from 'formik';
import * as Yup from 'yup';
var r = /^(ftp|http|https):\/\/[^ "]+$/;
const updateProfile = Yup.object().shape({
    name: Yup.string()
    .required('Tn ngi dng bt buc phi nhp')
    .max(20, 'Ti a tn 20 k t'),

    web_url: Yup.string()
    .matches(r, 'URL khng ng nh dng')
    .max(50, 'Ti a URL website 50 k t'),

    story: Yup.string()
    .max(200, 'Ti a tiu s 200 k t'),
    location: Yup.string()
    .max(50, 'Ti a 50 k t'),
    learning: Yup.string()
    .max(100, 'Ti a 100 k t'),
    work: Yup.string()
    .max(100, 'Ti a tiu s 100 k t'),
    education: Yup.string()
    .max(100, 'Ti a tiu s 100 k t'),

  });

  const validChangpasswprd = Yup.object().shape({
    passold: Yup.string()
    .required('Vui lng nhp mt khu'),

    passnew: Yup.string()
    .required('Vui lng khng  trng')
    .min(8, 'Mt khu ti thiu 8 k t'),

    cfpassnew: Yup.string()
    .required('Vui lng khng  trng')
    .oneOf([Yup.ref('passnew'), null], 'Confirm mt khu khng khp')

  });
  const validresertpass = Yup.object().shape({
    passnew: Yup.string()
    .required('Vui lng khng  trng')
    .min(8, 'Mt khu ti thiu 8 k t'),

    cfpassnew: Yup.string()
    .required('Vui lng khng  trng')
    .oneOf([Yup.ref('passnew'), null], 'Confirm mt khu khng khp')

  });

export const valid = {updateProfile,validChangpasswprd,validresertpass}