import { yupToFormErrors } from 'formik';
import * as Yup from 'yup';

const addtag = Yup.object().shape({
    name: Yup.string()
    .required('vui lng nhp tn th')
    .max(20, 'Ti a tn 20 k t'),

    description: Yup.string()
    .required('vui lng nhp m t th')
    .max(1000, 'Ti a m t 1000 k t'),

    abount: Yup.string()
    .required('vui lng nhp gii thiu th')
    .max(1000, 'Ti a gii thiu 1000 k t'),
    moderators: Yup.string()
    .required('vui lng chn qun tr th'),

  });


export const valid = {addtag}